import * as utils from '../utils';

let adsShownCount = 0;

export function rewardedVideo(userId) {
  return new Promise((resolve) => {
    const isNeedShown = isNeedShowAds();
    adsShownCount++;

    if (!isNeedShown) {
      return resolve();
    }

    const appId = 6757551;

    if (!window.admanInit) {
      return resolve('admanInit is unrefined');
    }

    const { admanInit } = window;
    admanInit({
      user_id: userId,
      app_id: appId,
      mobile: true,
      //params: {preview: 1},
      type: 'rewarded'
    }, (adman) => {
      adman.onCompleted(() => resolve(true));
      adman.onSkipped(() => resolve('skipped'));
      adman.start('preroll');
    }, () => {
      resolve('no ads');
      utils.statReachGoal('no_ads');
    });
  });
}

export function isNeedShowAds() {
  return adsShownCount % 2 === 0;
}


