import React, { Component } from 'react';
import SVG from 'react-inlinesvg';
import * as utils from '../utils';
import { Status } from '../constants';

export default class TabBar extends Component {

  static get icons() {
    return {
      roulette: require('../asset/refresh_24.svg'),
      dialogs: require('../asset/message_24.svg'),
      settings: require('../asset/settings_24.svg')
    }
  }

  render() {
    return (
      <div className="TabBar">
        {this._renderItems()}
      </div>
    )
  }

  _renderItems() {
    let activeTab = 'roulette';
    if (this.props.state.status === Status.dialogs) {
      activeTab = 'dialogs';
    } else if (this.props.state.status === Status.settings) {
      activeTab = 'settings';
    }

    return [{
      type: 'roulette',
      title: 'Рулетка',
    }, {
      type: 'dialogs',
      title: 'Диалоги',
    }, {
      type: 'settings',
      title: 'Настройки',
    }].map((item) => {
      const className = utils.classNames({
        TabBar__item: true,
        active: activeTab === item.type,
        badge: item.type === 'dialogs' && this.props.state.hasBadge
      });
      return (
        <div className={className} key={item.type} onClick={() => this.props.onChange(item.type)}>
          <div className="TabBar__item_icon"><SVG src={TabBar.icons[item.type]} /></div>
          <div className="TabBar__item_label">{item.title}</div>
        </div>
      )
    });
  }
}


