import React, { Component } from 'react';
import './Dialogs.css';
import SVG from 'react-inlinesvg';
import * as api from '../../services/api';
import connect from '../../libs/vkui-connect';
import Notification from "../../components/Notification";
import emitter from "../../services/emitter";

export default class Dialogs extends Component {
  componentDidMount() {
    if (!this.data.isLoaded || this.data.isNeedLoad) {
      this._load();
    }
  }

  _load = () => {
    clearTimeout(this.loadTimer);
    this.props.setState({isLoading: true, isNeedLoad: false});
    api.method(api.methods.chats).then((list) => {
      this.props.setState({isLoading: false, list, isLoaded: true});
    }).catch(() => {
      clearTimeout(this.loadTimer);
      this.loadTimer = setTimeout(this._load, 5000);
    });
  };

  get data() {
    return this.props.state.dialogs;
  }

  render() {
    return (
      <div className="Page__wrap">
        {this._renderNotify()}
        {this._renderCont()}
      </div>
    )
  }

  _renderCont() {
    const state = this.props.state.dialogs;

    if (state.isLoading) {
      return (
        <div className="Dialogs__loader">
          <SVG src={require('../../asset/spinner_24.svg')} />
        </div>
      )
    }

    if (state.list.length === 0) {
      return (
        <div className="Dialogs__empty">Начни общаться в рулетке!</div>
      )
    }

    return (
      <div className="Dialogs">
        {this._renderDialogs()}
      </div>
    )
  }

  _renderNotify() {
    if (!this.props.state.isNeedNotifyRequest || this.data.list.length === 0 || this.data.isLoading) {
      return null;
    }


    return (
      <Notification
        type="notify"
        title="Уведомления"
        caption="Включи и не пропусти, когда кто-нибудь напишет"
        btnText="Включить"
        onClick={() => emitter.emit('requestNotifications')}
      />
    )
  }

  _renderDialogs() {
    return this.data.list.map((dialog) => {
      return (
        <div className="Dialogs__item" key={dialog.user.id} onClick={() => this.props.openDialog(dialog.user)}>
          <div className="Dialogs__item_photo" style={{backgroundImage: `url(${dialog.user.photos && dialog.user.photos.length > 0 && dialog.user.photos[0]})`}} />
          <div className="Dialogs__item_cont">
            <div className="Dialogs__item_name">{dialog.user.name}</div>
            <div className="Dialogs__item_message">{dialog.message}</div>
          </div>
          {dialog.unread > 0 && <div className="Dialogs__item_badge">{dialog.unread}</div>}
        </div>
      )
    });
  }
}



