import React, { Component } from 'react';
import '@vkontakte/vkui/dist/vkui.css';
import './style.css';
import * as api from './services/api';
import { setEventHandler } from './realtime';
import * as payment from './payments';
import Chat from './containers/Chat';
import Rates from './containers/Rates';
import Settings from './containers/Settings/Settings';
import Filters from './containers/Filters/Filters';
import PremiumBox from './containers/PremiumBox/PremiumBox';
import Dialogs from './containers/Dialogs/Dialogs';
import * as utils from './utils';
import connect from './libs/vkui-connect';
import emitter from './services/emitter';
import * as ads from './services/ads';
import SVG from 'react-inlinesvg';
import TabBar from "./components/TabBar";
import { Status } from './constants';
import HeadMenu, {HeadMenuItem} from "./components/HeadMenu";
import Alert from "./components/Alert";


let SkipChatUser = {};

class App extends Component {
  constructor() {
    super();

    this.connectingChatId = null;

    this.state = {
      loadingPayments: false,
      loadingPaymentsFailed: false,
      availChats: 0,
      status: window.isTargetDialogs ? Status.dialogs : Status.nothing,
      messages: [],
      isMultiConnections: false,
      buttonType: '',
      onlineCount: 0,
      hasPremium: !utils.isPaymentsEnabled(),
      filters: {
        gender: 'all',
        ageFrom: 14,
        ageTo: 80
      },
      isPremiumBoxShown: false,
      vkInfo: window.VkInfo,
      hasBadge: false,
      user: {
        name: 'Test',
        bdate: '31.01.1995',
        city: 'Moscow',
        isVip: true,
        photos: [
          'https://sun1-23.userapi.com/c855224/v855224035/166b7/v1gBZcv3o9Y.jpg',
          'https://pp.userapi.com/c851420/v851420808/ed655/btjtdLMkbBM.jpg'
        ]
      },
      isLoaderShown: false,
      dialogs: {
        list: [],
      },
      chat: {
        isLoading: false,
      },
      isMenuShown: false,
      alert: false,
      isNeedNotifyRequest: false
    };
  }

  componentDidMount() {
    setEventHandler(this._eventDidReceive);
    this._loadPayments();

    this.listeners = [];
    this.listeners.push(emitter.addListener('togglePremiumBox', (shown = false) => {
      //connect.send('OpenPremium', {});
      this.setState({isPremiumBoxShown: shown});
    }));
    this.listeners.push(emitter.addListener('vkInfoUpdated', (vkInfo) => this.setState({vkInfo})));
    this.listeners.push(emitter.addListener('requestNotifications', () => {
      connect.send('VKWebAppAllowMessagesFromGroup', {group_id: 174149088});
      this.setState({isNeedNotifyRequest: false});
    }));
  }

  componentWillUnmount() {
    for (let listener of this.listeners) {
      listener.remove();
    }
    this.listeners = [];
  }

  componentWillUpdate(nextProps, nextState) {
    if (nextState.status !== this.state.status) {
      if (nextState.status === Status.loading || nextState.status === Status.connecting) {
        connect.send('ShowAds', {});
      } else {
        connect.send('HideAds', {});
      }
    }
  }

  render() {
    const classNames = utils.classNames({
      App: true,
      android: utils.isAndroid()
    });
    return (
      <div className={classNames}>
        <div className="App__header">
          <div className="App__header__content">
            {this._renderHeaderButton()}
            <div className="App__header__logo">{this._getTitle()}</div>
            {false && window.VkInfo.sex === 2 && !this.state.loadingPayments && <div className="App__header__balance">{utils.availChatsStr(this.state.availChats)}</div>}
          </div>
        </div>
        {this._renderGetContent()}
        {this._renderPremium()}
        {this._renderLoader()}
        {this._renderTabBar()}
        {this._renderMenu()}
        {this._renderAlert()}
      </div>
    );
  }

  _renderMenu() {
    if (!this.state.isMenuShown) {
      return null;
    }

    return (
      <HeadMenu onHide={() => this.setState({isMenuShown: false})}>
        <HeadMenuItem
          icon={require('./asset/delete_24.svg')}
          onClick={() => {
            this.setState({
              isMenuShown: false,
              alert: {
                title: 'Ты действительно хочешь удалить диалог?',
                message: 'Диалог пропадёт, история переписки очистится, собеседник больше не сможет написать',
                yesBtn: 'Да, удалить',
                onConfirm: () => {
                  if (this.state.status !== Status.chat || !this.state.chat.isPersistent) {
                    return;
                  }
                  api.method(api.methods.deleteChat, {
                    peer_id: this.state.user.id
                  }).then(() => {
                    this.setDialogsState({list: [], isNeedLoad: true});
                  });
                  setTimeout(() => this._exitFromChat(), 100);
                }
              }
            });
          }}
        >Удалить диалог</HeadMenuItem>
      </HeadMenu>
    )
  }

  _renderAlert() {
    if (!this.state.alert) {
      return null;
    }

    return <Alert
      title={this.state.alert.title}
      yesBtn={this.state.alert.yesBtn}
      onHide={() => this.setState({alert: false})}
      onConfirm={() => {
        this.state.alert.onConfirm();
        this.setState({alert: false});
      }}
    >{this.state.alert.message}</Alert>;
  }

  _renderLoader() {
    if (!this.state.isLoaderShown) {
      return null;
    }

    return (
      <div className="Chat__loader">
        <div className="Chat__loader__cont">
          <div className="Chat__loader__icon">
            <SVG src={require('./asset/spinner.svg')} />
          </div>
        </div>
      </div>
    )
  }

  _renderGetContent() {
    if (this.state.isMultiConnections) {
      //return this._renderStatusLoading();
    }

    switch (this.state.status) {
      case Status.nothing:
        return this._renderStatusNothing();
      case Status.loading:
      case Status.connecting:
      case Status.leave:
      case Status.error:
        return this._renderStatusLoading();
      case Status.chat:
        return <Chat
          ref="chat"
          state={this.state}
          skip={() => this._leaveChat()}
          sendMessage={this._sendMessage}
        />;
      case Status.payment:
        return <Rates state={this.state} showOrder={this._showOrder} />;
      case Status.settings:
        return <Settings {...this.state} showFilters={() => this.showFilters(Status.settings)} />;
      case Status.filters:
        return <Filters {...this.state} applyFilters={this.applyFilters} setHashPremium={this.setHashPremium} />;
      case Status.dialogs:
        return <Dialogs state={this.state} setState={this.setDialogsState} openDialog={this.openDialog} />;
    }
  }

  _renderStatusNothing() {
    const buttonClassName = utils.classNames({
      Status__into__button: true,
      loading: this.state.loadingPayments,
      failed: this.state.loadingPaymentsFailed
    });

    let buttonText = 'Начать';
    if (this.state.loadingPayments) {
      buttonText = 'Загрузка…';
    }

    if (this.state.loadingPaymentsFailed) {
      buttonText = 'Ошибка';
    }

    const onlineCount = utils.gram(this.state.onlineCount, ['незнакомец', 'незнакомца', 'незнакомцев']);
    const wantGram = utils.gram(this.state.onlineCount, ['хочет', 'хотят', 'хотят'], true);

    return (
      <div className="Status__wrap">
        <div className="Status__into">
          <div className="Status__into__icon" />
          <div className="Status__into__caption"><span>{onlineCount}</span> онлайн и {wantGram} пообщаться с тобой. Сыграй в рулетку и зарядись позитивом!</div>
          <div className={buttonClassName} onClick={this._start}>{buttonText}</div>
        </div>
      </div>
    )
  }

  _renderStatusLoading() {
    let title = 'Ищем свободного собеседника…';
    let className = '';

    if (this.state.isMultiConnections) {
      title = 'Похоже, у тебя рулетка запущена ещё с одного устройства или браузера. Оставь одну!';
      className = 'multi_connect';
    //} else if (this.state.status === Status.connecting) {
    //  title= 'Соединение';
    } else if (this.state.status === Status.leave) {
      title= <span>Собеседник слился :(<br />Ищем другого…</span>;
      className = 'leave';
    } else if (this.state.status === Status.error) {
      title=  'Что-то пошло не так';
    }

    return (
      <div className="Status__wrap">
        <div className={`Status__loading ${className}`}>
          <div className="Status__loading__icon">
            <div className="Status__loading__indicator">
              <svg width="104" height="104" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M0 0a52 52 0 0 1 52 52h-2.002A49.997 49.997 0 0 0 0 2.002V0z" fill="currentColor"/><defs><linearGradient id="paint0_linear" x1="-52" y1="104" x2="52" gradientUnits="userSpaceOnUse"><stop/><stop offset="1"/><stop offset="1"/></linearGradient></defs></svg>
            </div>
          </div>
          <div className="Status__loading__title">{title}</div>
        </div>
      </div>
    )
  }

  _start = () => {
    utils.statReachGoal('start');

    if (this.state.loadingPaymentsFailed) {
      //return this._loadPayments();
    }

    if (window.VkInfo.sex === 2 && !this.state.availChats) {
      //return this.setState({status: Status.payment});
    }

    this.setState({status: Status.loading});
    api.vk('photos.get', {
      album_id: 'profile',
      rev: 1,
      count: 5,
      photo_sizes: 1
    }).then((resp) => {
      let photos = [];
      for (let i = 0; i < resp.items.length; i++) {
        const item = resp.items[i];
        let src = false;
        let lastSize = 0;
        for (let j = 0; j < item.sizes.length; j++) {
          const size = item.sizes[j];
          if (size.width > lastSize) {
            src = size.url;
            lastSize = size.width;
          }
          if (lastSize >= 600) {
            break;
          }
          if (['x', 'y'].indexOf(size.type) > -1) {
            src = size.url;
            break;
          }
        }
        if (!src) {
          continue;
        }
        photos.unshift(src);
      }

      window.VkInfo.photos = photos;

      if (this.state.status !== Status.loading) {
        return;
      }

      if (!window.isNotificationsEnabled) {
        connect.send('VKWebAppAllowNotifications', {});
        window.isNotificationsEnabled = true;
      }
      this._checkChats();
    }).catch(() => {
      if (this.state.status !== Status.loading) {
        return;
      }

      window.VkInfo.photos = [];
      this._checkChats();
    });
  };

  _stopCheck = () => {
    this.connectingChatId = null;
    clearTimeout(this.checkChatsTimer);
    clearTimeout(this.useTimer);
  };

  _checkChats = (status = Status.loading) => {
    this._stopCheck();

    if (window.VkInfo.sex === 2 && !this.state.availChats) {
      //return this.setState({status: Status.payment});
    }

    this.setState({status});
    const info = window.VkInfo;

    let age = 0;
    const bdExp = String(info.bdate).split('.');
    if (bdExp.length === 3) {
      age = utils.getUsrAge(new Date(parseInt(bdExp[2], 10), parseInt(bdExp[1], 10) - 1, parseInt(bdExp[0], 10)) / 1000);
    }

    api.method(api.methods.chat, {
      name: info.first_name,
      photos: info.photos.length > 0 ? info.photos : [info.photo_400_orig],
      gender: info.sex,
      bdate: info.bdate,
      education: info.university_name || '',
      city: info.city ? info.city.title : '',
      group_id: window.GroupId,
      age,
      filter_gender: this.state.filters.gender === 'all' ? 0 : (this.state.filters.gender === 'male' ? 2 : 1),
      filter_age_from: this.state.filters.ageFrom,
      filter_age_to: this.state.filters.ageTo
    }).then((resp) => {
      clearTimeout(this.checkChatsTimer);
      if (this.state.status !== status) {
        return;
      }
      if (resp.chat) {
        this.connectingChatId = resp.chat;
        this.setState({status: Status.connecting});
        this.checkChatsTimer = setTimeout(this._checkChats, 7000);
      } else {
        this.checkChatsTimer = setTimeout(this._checkChats, 2000);
      }
    }).catch(() => {
      this.checkChatsTimer = setTimeout(this._checkChats, 3000);
    });
  };

  _eventDidReceive = (type, event) => {
    switch (type) {
      case 'check':
        this._checkEventDidReceive(parseInt(event.fromId, 10));
        break;
      case 'connect':
        this._connectEventDidReceive(parseInt(event.fromId, 10), event.user, parseInt(event.force, 10) === 1);
        break;
      case 'reject':
        this._rejectEventDidReceive(parseInt(event.fromId, 10));
        break;
      case 'leave':
        this._leaveEventDidReceive(parseInt(event.fromId, 10));
        break;
      case 'message':
        this._messageEventDidReceive(parseInt(event.fromId, 10), event.text, event.attach, event.user);
        break;
      case 'multi_connections':
        this._multiConnectionsEventDidReceive();
        break;
      case 'online':
        this.setState({onlineCount: parseInt(event.count, 10)});
        break;
      case 'typing':
        this._typingEventDidReceive(parseInt(event.fromId, 10));
        break;
      case 'filters':
        this.setState({
          filters: event.filters,
          hasPremium: event.hasPremium || utils.isIOS(),
          hasBadge: parseInt(event.badge, 10) === 1,
          isNeedNotifyRequest: parseInt(event.notify, 10) === 0
        });
        utils.applyColor(event.color);
        break;
      case 'connected':
        if (window.isMultiConnectionError) {
          this.setState({isMultiConnections: false});
          window.isMultiConnectionError = false;
        }
        break;
    }
  };

  _typingEventDidReceive = (fromId) => {
    if (this.connectingChatId === fromId) {
      this.refs.chat && this.refs.chat.showTyping();
    }
  };

  _multiConnectionsEventDidReceive = () => {
    if (this.state.status !== Status.nothing) {
      this.connectingChatId = null;
      clearTimeout(this.checkChatsTimer);
      clearTimeout(this.useTimer);
      this.setState({status: Status.nothing});
    }
    this.setState({isMultiConnections: true});
  };

  _checkEventDidReceive(fromId) {
    if (this.state.status === Status.loading || this.state.status === Status.leave) {
      clearTimeout(this.checkChatsTimer);
      this.connectingChatId = fromId;
      this.setState({status: Status.connecting});
      api.method(api.methods.accept, {id: fromId}).catch((err) => console.log('accept error', err));
      console.log('_checkEventDidReceive: send accept');
    } else {
      api.method(api.methods.reject, {id: fromId}).catch((err) => console.log('reject error', err));
      console.log('_checkEventDidReceive: send reject');
    }
  }

  _connectEventDidReceive(fromId, user, force) {
    if (this.state.status === Status.connecting && (force || this.connectingChatId === fromId)) {
      this.setState({status: Status.chat, user, messages: [], chat: {}});
      clearTimeout(this.checkChatsTimer);
      if (!force) {
        this.useTimer = setTimeout(() => this._useChat(fromId), 5000);
      }
      utils.statReachGoal('connect');
    } else {
      console.log('_connectEventDidReceive: send leave');
      api.method(api.methods.leave, { id: fromId }).catch((err) => console.log('leave error', err.message));
    }
  }

  _rejectEventDidReceive(fromId) {
    if (this.state.status === Status.connecting && this.connectingChatId === fromId) {
      this._checkChats();
    }
  }

  _leaveEventDidReceive(fromId) {
    if (this.state.status === Status.chat && parseInt(this.state.user.id, 10) === fromId && !this.state.chat.isPersistent) {
      clearTimeout(this.checkChatsTimer);
      this.setState({status: Status.leave});
      this._checkChats(Status.leave);
      console.log('_leaveEventDidReceive');
    }
  }

  _messageEventDidReceive(fromId, text, attach, user) {
    let needIncrBadge = true;
    if (this.state.status === Status.chat && parseInt(this.state.user.id, 10) === fromId) {
      const messages = this.state.messages;
      messages.push({isInbox: true, text, attach});
      this.setState({messages});
      Chat.scrollToBottom(true);
      this.refs.chat && this.refs.chat.hideTyping();
      needIncrBadge = false;
    } else {
      api.method(api.methods.leave, { id: fromId }).catch((err) => console.log('leave error', err));
    }

    let found = false;
    for (let i = 0; i < this.state.dialogs.list.length; i++) {
      let dialog = this.state.dialogs.list[i];
      if (dialog.user.id === fromId) {
        this.state.dialogs.list[i].message = text;
        if (needIncrBadge) {
          this.state.dialogs.list[i].unread += 1;
        }
        this.setDialogsState({list: this.state.dialogs.list});
        found = true;
        break;
      }
    }

    if (!found) {
      let list = this.state.dialogs.list;
      list.unshift({
        unread: needIncrBadge ? 1 : 0,
        message: text,
        user
      })
    }

    if (this.state.status !== Status.dialogs) {
      this.setState({hasBadge: true});
    }
  };

  _leaveChat = (skipSearch = false) => {
    api.method(api.methods.leave, { id: this.state.user.id }).catch((err) => console.log(err));
    if (this.state.hasPremium && utils.isPaymentsEnabled() || skipSearch || window.isNative || true) {
      return this._doLeaveChat(skipSearch);
    }

    this.setState({isLoaderShown: true});
    ads.rewardedVideo(window.VkInfo.id).then((status) => {
      this.setState({isLoaderShown: false});
      this._doLeaveChat(skipSearch);

      if (status === true) {
        utils.statReachGoal('ads_seen');
      }
    });
  };

  _doLeaveChat = (skipSearch) => {
    if (!skipSearch) {
      this._checkChats();
    }
    utils.statReachGoal('skip');
  };

  _loadPayments = () => {
    if (window.VkInfo.sex === 1 || true) {
      return this.setState({loadingPayments: false});
    }
    this.setState({loadingPaymentsFailed: false, loadingPayments: true});
    payment.getAvailableChats()
      .then((availChats) => this.setState({availChats, loadingPayments: false}))
      .catch(() => this.setState({loadingPaymentsFailed: true, loadingPayments: false}));
  };

  _useChat = (fromId) => {
    if (SkipChatUser[fromId] || true) {
      return;
    }
    SkipChatUser[fromId] = true;
    const availChats = this.state.availChats - 1;
    this.setState({availChats});
    payment.useChat();
  };

  _showOrder = (rate, count) => {
    if (!count) {
      return;
    }
    payment.showOrderBox(rate).then(() => {
      payment.incrChats(count);
      const availChats = this.state.availChats + count;
      this.setState({availChats});
      this._checkChats();
    }).catch((err) => {
      alert('Проишошла ошибка');
    });
  };

  _sendMessage = (text, extra) => {
    return new Promise((resolve, reject) => {
      let hasInbox = false;
      for (let i = 0; i < this.state.messages.length; i++) {
        if (this.state.messages[i].isInbox) {
          hasInbox = true;
          break;
        }
      }

      api.method(api.methods.message, {
        text,
        attach: extra.attach || '',
        id: this.state.user.id,
        save: hasInbox ? 1 : 0
      }).then(() => {
        let messages = this.state.messages;
        messages.push({
          text,
          attach: extra.attach,
          isInbox: false
        });
        this.setState({messages});
        resolve();
        utils.statReachGoal('message_sent');
      }).catch((err) => {
        if (err.error === 'deleted') {
          this.setState({chat: Object.assign({}, this.state.chat, {isDeleted: true})});
        }
        reject();
        utils.statReachGoal('message_failed');
      });
    });
  };

  _renderHeaderButton = () => {
    let icon;
    if (this.state.status === Status.nothing) {
      icon = <SVG src={require('./asset/filter_24.svg')} />
    } else if (this.state.status === Status.dialogs || this.state.status === Status.settings) {
      icon = false;
    } else {
      icon = (
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M6 12H20" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
          <path d="M11.5 18.5L5 12L11.5 5.5" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round"/>
        </svg>
      )
    }

    if (!icon) {
      return null;
    }

    return (
      <div className="App__header__btn" onClick={this._headBtnDidPress}>{icon}</div>
    )
  };

  _getTitle = () => {
    if (this.state.status === Status.leave || this.state.status === Status.loading || this.state.status === Status.connecting) {
      return 'Поиск';
    } else if (this.state.status === Status.chat) {
      if (this.state.chat.isPersistent || true) {
        const arrowClass = utils.classNames({
          Header__arrow: true,
          reverse: this.state.isMenuShown
        });
        return <div className="Header__arrow__wrap" onClick={() => this.setState({isMenuShown: !this.state.isMenuShown})}>
            {this.state.user.name}
            <div className={arrowClass}>
              <SVG src={require('./asset/arrow_16.svg')} />
            </div>
          </div>;
      } else {
        return this.state.user.name;
      }
    } else if (this.state.status === Status.settings) {
      return 'Настройки';
    } else if (this.state.status === Status.filters) {
      return 'Фильтры';
    } else if (this.state.status === Status.dialogs) {
      return 'Диалоги';
    }
    return 'Рулетка';
  };

  _exitFromChat = () => {
    this._stopCheck();
    if (this.state.status === Status.chat) {
      this._leaveChat(true);
    }

    if (this.state.status === Status.chat && this.state.chat.isPersistent) {
      this.setState({status: Status.dialogs});
    } else {
      this.setState({status: Status.nothing});
      this._updateOnline();
    }

    this._resetBadge();
  };

  _headBtnDidPress = () => {
    if (this.state.status === Status.leave || this.state.status === Status.loading || this.state.status === Status.connecting || this.state.status === Status.chat) {
      this._exitFromChat();
    } else if (this.state.status === Status.settings || this.state.status === Status.filters) {
      if (this.state.prevStatus === Status.settings) {
        this.setState({status: Status.settings});
      } else {
        this.setState({status: Status.nothing});
        this._updateOnline();
      }
    } else if (this.state.status === Status.nothing) {
      this.showFilters();
    }
  };

  _openSettings = () => {
    this.setState({status: Status.settings});
  };

  _updateOnline = () => {
    api.method(api.methods.updateOnline).catch((err) => console.log(err));
  };

  showFilters = (target = Status.roulette) => {
    this.setState({status: Status.filters, prevStatus: target});
  };

  applyFilters = (filters) => {
    this.setState({filters});
    this._start();
  };

  setHashPremium = () => {
    this.setState({hasPremium: true});
  };

  _renderPremium() {
    if (!this.state.isPremiumBoxShown) {
      return null;
    }

    return <PremiumBox state={this.state} setHashPremium={this.setHashPremium} />;
  }

  _renderTabBar() {
    if ([Status.nothing, Status.dialogs, Status.settings].indexOf(this.state.status) === -1) {
      return null;
    }

    return <TabBar
      state={this.state}
      onChange={(page) => {
        const statusMap = {
          roulette: Status.nothing,
          dialogs: Status.dialogs,
          settings: Status.settings
        };

        if (page === 'dialogs') {
          this._resetBadge();
        }

        if (this.state.status === statusMap[page]) {
          return;
        }

        this.setState({status: statusMap[page]});

        if (page === 'roulette') {
          this._updateOnline();
        }
      }}
    />
  }

  setDialogsState = (updated) => {
    this.setState({
      dialogs: Object.assign({}, this.state.dialogs, updated)
    });
  };

  openDialog = (user) => {
    this.setState({
      user,
      status: Status.chat,
      messages: [],
      chat: {
        isLoading: true,
        isPersistent: true
      }
    });

    api.method(api.methods.history, {
      peer_id: user.id
    }).then(({history, can_send}) => {
      if (this.state.user && this.state.user.id === user.id) {
        this.setState({messages: history.reverse(), chat: Object.assign({}, this.state.chat, {isLoading: false, isDeleted: !can_send})}, () => {
          Chat.scrollToBottom(true, true);
        });
      }

      for (let i = 0; i < this.state.dialogs.list.length; i++) {
        let dialog = this.state.dialogs.list[i];
        if (dialog.user.id === user.id) {
          this.state.dialogs.list[i].unread = 0;
          this.setDialogsState({list: this.state.dialogs.list});
          break;
        }
      }
    }).catch(() => {
      if (this.state.user && this.state.user.id === user.id) {
        this.setState({chat: Object.assign({}, this.state.chat, {isLoading: false})});
      }
    });
  };

  _resetBadge = () => {
    if (!this.state.hasBadge) {
      return;
    }

    api.method(api.methods.resetBadge);
    this.setState({hasBadge: false});
  };
}

export default App;



