import React, { Component } from 'react';
import SVG from 'react-inlinesvg';
import './Settings.css';
import connect from '../../libs/vkui-connect';
import * as utils from '../../utils';
import * as api from '../../services/api';
import Notification from "../../components/Notification";
import emitter from "../../services/emitter";

export default class Settings extends Component {
  constructor(props) {
    super(props);

    this.state = {
      color: window.color || 'purple'
    };
  }

  render() {
    return (
      <div className="Page__wrap">
        {!this.props.hasPremium && !utils.isIOS() && <Notification
          type="vip"
          title="Рулетка VIP"
          caption="Стань круче с отметкой VIP, фильтрами и без рекламы"
          btnText="Получить"
          onClick={() => emitter.emit('togglePremiumBox', true)}
        />}
        <div className="Settings__rows">
          <div className="Settings__row">
            <div className="Settings__row__icon">
              <SVG src={require('../../asset/appearance_24.svg')} />
            </div>
            <div className="Settings__row__label">Тема оформления</div>
          </div>
          <div className="Settings__palette_items">
            {this._renderPalette()}
          </div>
          <div className="Settings__row" onClick={this.props.showFilters}>
            <div className="Settings__row__icon">
              <SVG src={require('../../asset/filter_24.svg')} />
            </div>
            <div className="Settings__row__label">Фильтр собеседников</div>
            {utils.isPaymentsEnabled() && <div className="Settings__row__badge">VIP</div>}
          </div>
          {!window.isNative && <div className="Settings__row" onClick={() => connect.send('VKWebAppAddToFavorites', {})}>
            <div className="Settings__row__icon">
              <SVG src={require('../../asset/star_24.svg')} />
            </div>
            Добавить в избранное
          </div>}
          <div className="Settings__row" onClick={() => connect.send('VKWebAppShare', {})}>
            <div className="Settings__row__icon">
              <SVG src={require('../../asset/share_24.svg')} />
            </div>
            Поделиться с друзьями
          </div>
          {this._renderLinks()}
          {window.isNative && <div className="Settings__row" onClick={() => connect.send('LogOut', {})}>
            <div className="Settings__row__icon">
              <SVG src={require('../../asset/logout_24.svg')} />
            </div>
            Выйти
          </div>}
        </div>
      </div>
    )
  }

  _renderLinks() {
    if (window.isNative) {
      return (
        <div>
          <div onClick={() => connect.send('OpenLink', {url: 'https://vk.me/rouletteapp'})} className="Settings__row">
            <div className="Settings__row__icon">
              <SVG src={require('../../asset/message_24.svg')} />
            </div>
            Сообщить о проблеме
          </div>
          <div onClick={() => connect.send('OpenLink', {url: 'https://vk.com/rouletteapp'})} className="Settings__row">
            <div className="Settings__row__icon">
              <SVG src={require('../../asset/community_24.svg')} />
            </div>
            Сообщество приложения
          </div>
        </div>
      )
    }

    return (
      <div>
        <a href="https://vk.me/rouletteapp" target="_blank" className="Settings__row">
          <div className="Settings__row__icon">
            <SVG src={require('../../asset/message_24.svg')} />
          </div>
          Сообщить о проблеме
        </a>
        <a href="https://vk.com/rouletteapp" target="_blank" className="Settings__row">
          <div className="Settings__row__icon">
            <SVG src={require('../../asset/community_24.svg')} />
          </div>
          Сообщество приложения
        </a>
      </div>
    )
  }

  _renderPalette() {
    return [
      'red',
      'orange',
      'green',
      'cyan',
      'blue',
      'purple',
      'pink',
      'grey'
    ].map((color) => {
      const className = utils.classNames({
        Settings__palette_item: true,
        [color]: true,
        selected: this.state.color === color
      });
      return (
        <div
          key={color}
          className={className}
          onClick={() => {
            this.setState({color});
            utils.applyColor(color);
            api.method(api.methods.saveColor, {color}).catch((err) => console.log(err));
          }}
        >
          <div className="Settings__palette_item_selected">
            <SVG src={require('../../asset/done_24.svg')} />
          </div>
        </div>
      )
    });
  }
}



