import * as utils from './utils';

let lastEventId = 0;
let socket = null;

let ppTimer = null;
let pongTimer = null;
let eventHandler = false;
let closedByClient = false;
let reconnectTimer = false;

export function setEventHandler(handler) {
  eventHandler = handler;
}

export default function init() {
  connect();
}

function connect() {
  reconnectTimer && clearTimeout(reconnectTimer);
  let params = {
    last_event_id: lastEventId
  };

  if (window.VkToken) {
    params.token = window.VkToken;
  } else if (window.VkAppsSign) {
    params.sign = window.VkAppsSign;
    params.url = window.VkInitialUrl;
  }

  if (window.isNative) {
    params.is_native = 1;
  }

  let paramsArr = [];
  for (let i in params) {
    paramsArr.push(`${i}=${encodeURIComponent(params[i])}`);
  }

  let port = 8098;
  if (utils.isDev()) {
    port = 8097;
  }

  closedByClient = false;
  // 'wss://dateapp.ru/live_chats_ws/?' + paramsArr.join('&')
  socket = new WebSocket('wss://dateapp.ru:'+ port +'/echo?' + paramsArr.join('&'));

  socket.onopen = function() {
    console.log('ws connected');
  };

  socket.onclose = () => {
    console.log('closed');
    connectionDidClose();
  };
  socket.onmessage = messageDidReceive;

  socket.onerror = function(error) {
    console.log("ws err:", error.message);
    if (!closedByClient) {
      connectionDidClose();
    }
  };
}

function connectionDidClose() {
  reconnectTimer && clearTimeout(reconnectTimer);
  reconnectTimer = setTimeout(connect, 3000);
  clearTimeout(ppTimer);
  clearTimeout(pongTimer);
}

function messageDidReceive(e) {
  const messages = e.data.split('\n');
  for (let i = 0; i < messages.length; i++) {
    let event;
    try {
      event = JSON.parse(messages[i]);
    } catch(e) {
      console.log('err', e.message);
      continue;
    }

    lastEventId = event.id;

    if (eventHandler) {
      eventHandler(event.type, event.data);
    }
  }
}



