import React from 'react';
import SVG from 'react-inlinesvg';

export default class HeadMenu extends React.PureComponent {
  render() {
    const height = (document.querySelector('.App__header__btn') || {}).offsetHeight;
    return (
      <div className="HeadMenu__wrap" onClick={(e) => this._onClick(e)}>
        <div className="HeadMenu" style={{top: height}}>
          <div className="HeadMenu__cont">
            {this.props.children}
          </div>
        </div>
      </div>
    )
  }

  _onClick = (e) => {
    if (e.target.classList.contains('HeadMenu__wrap') || e.target.classList.contains('HeadMenu')) {
      this.props.onHide();
    }
  };
}

export class HeadMenuItem extends React.PureComponent {
  render() {
    return (
      <div className="HeadMenu__item" onClick={() => this.props.onClick()}>
        <div className="HeadMenu__item__icon">
          <SVG src={this.props.icon} />
        </div>
        <div className="HeadMenu__item__label">{this.props.children}</div>
      </div>
    )
  }
}


