import React from 'react';

export default class Alert extends React.PureComponent {
  render() {
    return (
      <div className="AlertBox">
        <div className="AlertBox__cont">
          <div className="AlertBox__title">{this.props.title}</div>
          <div className="AlertBox__message">{this.props.children}</div>
          <div className="AlertBox__buttons">
            <div className="AlertBox__button" onClick={this.props.onHide}>Отмена</div>
            <div className="AlertBox__button" onClick={this.props.onConfirm}>{this.props.yesBtn}</div>
          </div>
        </div>
      </div>
    )
  }
}



